import React, { Component } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import Select from 'react-select';
import { withAlert } from 'react-alert';
import { motion } from "framer-motion";

import { _getPresignedUrl, _uploadFileAws, _postOrganization } from '../../services/axios/api';
import OrganisationForm from '../../components/OrganisationForm';
import Modal from '../../components/uiElements/Modal';
import { helpers, animateConfigs } from '../../constants';
import { EditIcon, UploadOrganisation } from '../../components/svgComponents';
import Loading from '../../components/uiElements/Loading';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

class CreateOrganisation extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            organisationImage: null, 
            organisationImageRaw: null, 
            fieldTypeIndex: "",
            fileFormatIndex: "",
            stateIndex: "",
            cityIndex: "",
            fieldTitle: "",
            customInputs: [],
            organisationName: "",
            description: "",
            phoneNumber: "",
            email: "",
            website: "",
            facebook: "",
            instagram: "",
            loading: false,
        }

        this.fieldTypeOptions = [
            { value: 'text', label: 'Text Field' },
            { value: 'file', label: 'File upload' },
            { value: 'date', label: 'Date picker' }
        ]

        this.fileFormatOptions = [
            { value: 'image', label: 'Image' },
            { value: 'video', label: 'Video' },
            // { value: 'document', label: 'Document' }
        ]

        this.stateOptions = [
            { value: 'tamilnadu', label: 'Tamilnadu' },
            { value: 'maharashtra', label: 'Maharashtra' },
            { value: 'kerala', label: 'Kerala' }
        ]
    
        this.cityOptions = [
            { value: 'chennai', label: 'Chennai' },
            { value: 'mumbai', label: 'Mumbai' },
            { value: 'palakkad', label: 'Palakkad' }
        ]
    }

    onDropdownChange = (type, e) => {
        if(type === 'fieldType') {
            this.setState({fieldTypeIndex:helpers.getOptionIndex(e.value, this.fieldTypeOptions)});
        } else if(type === 'fileFormat') {
            this.setState({fileFormatIndex:helpers.getOptionIndex(e.value, this.fileFormatOptions)});
        } else if(type === 'state') {
            this.setState({stateIndex:helpers.getOptionIndex(e.value, this.stateOptions)});
        } else if(type === 'city') {
            this.setState({cityIndex:helpers.getOptionIndex(e.value, this.cityOptions)});
        }
    }

    handleAddCancelCustomField = (type) => {
        const { fieldTypeIndex, fileFormatIndex, fieldTitle, customInputs } = this.state;
        this.setState({showModal:false});
        if(type === 'add') {
            const copiedArr = [...customInputs];
            const data = {
                "name": this.fieldTypeOptions[fieldTypeIndex].value,
                "title": fieldTitle
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'text') {
                data["text"] = "";
                data["type"] = 0;
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'file') {
                data["fileType"] = this.fileFormatOptions[fileFormatIndex]?.value;
                data["type"] = 1;
            } 
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'date') {
                data["date"] = "02-04-2021";
                data["type"] = 2;
            }
            copiedArr.push(data);             
            this.setState({fieldTypeIndex: "", fileFormatIndex: "", fieldTitle: "", customInputs: copiedArr});
        } else {

        }
    }

    handleModifyInput = (type, i, value) => {
        const copiedArr = [...this.state.customInputs];
        if(type === 'delete') {
            const updatedArr = this.state.customInputs.filter((_, index) => i !== index);
            this.setState({customInputs:updatedArr});
        } else if(type === 'editFile') {
            copiedArr[i].file = value;
            this.setState({customInputs:copiedArr});
        } else if(type === 'editDate') {
            copiedArr[i].date = value;
            this.setState({customInputs:copiedArr});
        }
         else {
            copiedArr[i].text = value;
            this.setState({customInputs:copiedArr});
        }

    }

    onChangeText = (stateName, value) => {
        this.setState({[stateName]: value});
    }
     
    handleImage = async (e) => {
        const res = await helpers.readImage(e);
        this.setState({organisationImage:res[0], organisationImageRaw: e.target.files[0]});
    }

    buildImgTag = () => {
        return <div style={{display:'flex', marginRight:10}}>
            <img height='180px' width= '180px' style={{borderRadius:100}} src={this.state.organisationImage} alt="upload"/>
            {/* <p onClick={this.handleDeleteImage()} style={{marginLeft: -13, marginTop: -10, marginRight:10}}><CancelIcon/></p> */}
        </div>
    } 
    
    handleOrganisationCreation = async () => {
        this.setState({loading:true});
        const { organisationImageRaw, customInputs, organisationName, description, phoneNumber, email, website, facebook, instagram } = this.state;
        let data = {
            "name": organisationName,
            "description": description,
        }

        if(this.stateOptions[this.state.stateIndex]?.value) {
            data["state"] = this.stateOptions[this.state.stateIndex]?.value;
        }
        if(this.cityOptions[this.state.cityIndex]?.value) {
            data["city"] = this.cityOptions[this.state.cityIndex]?.value;
        }
        if(phoneNumber) {
            data["phone"] = phoneNumber;
        }
        if(email) {
            data["email"] = email;
        }
        if(website) {
            data["website"] = website;
        }
        if(facebook) {
            data["facebook"] = facebook;
        }
        if(instagram) {
            data["instagram"] = instagram;
        }
        
        const urlRes = await _getPresignedUrl({"module": "organization", "totalFiles": 1});
        const uploadRes = await _uploadFileAws(urlRes.data.urls[0].url, organisationImageRaw, organisationImageRaw.type);
        
        if(uploadRes.status === 200) {
            data["image"] = urlRes.data.urls[0].Key
        }
        const filterResArr = customInputs.filter(inp => inp.type === 1);
        if(filterResArr.length >= 1) {
            const fileUrlRes = await _getPresignedUrl({"module": "organization", "totalFiles": filterResArr.length});
            let i = 0;
            const returnPromises = customInputs.map(async (cI, index) => {
                if(cI.type === 1) {
                    delete cI['title'];
                    const uploadRes = await _uploadFileAws(fileUrlRes.data.urls[i].url, cI.file, cI.file.type);
                    if(uploadRes.status === 200) {
                        cI['file'] = fileUrlRes.data.urls[i].Key;
                        i = i + 1;
                    }
                }
            })
            Promise.all(returnPromises)
            .then(res => {
                data["custom"] = customInputs;
                console.log(data, 'data')
                this.addOrganization(data);
            })
            .catch(err => {
                this.props.alert.error('error occured', {timeout:2000});
                this.setState({loading:false});
            // console.log(err.response.data, 'err');
            })   
        }  else {
            this.addOrganization(data);
        } 
    }

    addOrganization = (data) => {
        _postOrganization(data)
        .then(res => {
            console.log(res, 'success');
            this.setState({loading:false});
            this.props.history.push('/');
            this.props.alert.success('Organization added successfully!', {timeout:2000});
        })
        .catch(err => {
            console.log(err.response.data, 'err');
            this.props.alert.error('error occured', {timeout:2000});
            this.setState({loading:false});
        })
    }

    customInput = () => {
        return <Row style={{marginBottom:20}}>
            <Col lg={12}>
                <Form.Label className={globalClasses.label}>
                    Field Type<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fieldType', e)}
                    value={this.fieldTypeOptions[this.state.fieldTypeIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fieldTypeOptions}
                />
            </Col>
            {this.fieldTypeOptions[this.state.fieldTypeIndex]?.value === "file" ? <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Upload Format<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fileFormat', e)}
                    value={this.fileFormatOptions[this.state.fileFormatIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fileFormatOptions}
                />
            </Col> : null}
            <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Field Title<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Form.Control type="text" placeholder="Type here" onChange={(e) => this.setState({fieldTitle: e.target.value})} value={this.state.fieldTitle} />
            </Col>
        </Row>
    }


    render() {

        if(this.state.loading) {
            return <Loading/>;
        }

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>
                        New Organization
                    </div>
                    <div>
                        <Button variant="outline-primary" style={{width:125}} className={globalClasses.outlinePrimaryBtn}>
                            Cancel
                        </Button>
                        <Button onClick={this.state.organisationName && this.state.organisationImage && this.state.description !== "" ? this.handleOrganisationCreation : null} style={{width:125, height:40, marginLeft:10, opacity: this.state.organisationName && this.state.organisationImage && this.state.description !== "" ? 1 : 0.5}} className={globalClasses.primaryBtn}>
                            Done
                        </Button>
                    </div>
                </div>
                <div style={{marginTop:25, display:'flex', justifyContent:'space-between'}}>
                    <div className={globalClasses.containerCard} style={{width:'28%', textAlign:'center', padding: "50px", height: "fit-content"}}>
                        <label htmlFor="addImg" style={{position:'relative', cursor:'pointer'}}>
                            <input onChange={(e) => this.handleImage(e)} id="addImg" hidden type="file" name="Pick an Image" accept="image/x-png,image/gif,image/jpeg"/>
                            {this.state.organisationImage ?
                                this.buildImgTag() :
                            <UploadOrganisation/>}
                            <div className={classes.editIcon}>
                                <EditIcon size={20}/>
                            </div>
                        </label>
                        <div style={{fontSize:16, fontWeight:600, opacity:0.7, marginTop:15}}>Click to upload photo</div>
                    </div>
                    <div className={globalClasses.containerCard} style={{width:'70%'}}>
                        <OrganisationForm
                            customInputs={this.state.customInputs}
                            modifyInput={this.handleModifyInput}
                            onChangeText={this.onChangeText}
                            organisationName={this.state.organisationName}
                            description={this.state.description}
                            phoneNumber={this.state.phoneNumber}
                            email={this.state.email}
                            website={this.state.website}
                            facebook={this.state.facebook}
                            instagram={this.state.instagram}
                            stateOptions={this.stateOptions}
                            cityOptions={this.cityOptions} 
                            onDropdownChange={this.onDropdownChange}
                            cityValue={this.cityOptions[this.state.cityIndex]}
                            stateValue={this.stateOptions[this.state.stateIndex]}
                        />
                        <Button onClick={() => this.setState({showModal:!this.state.showModal})} variant="outline-primary" style={{width:'100%', marginTop:20}} className={globalClasses.outlinePrimaryBtn}>
                            Add custom fields
                        </Button>
                    </div>
                </div>
                <Modal
                    title={'Add custom field'} 
                    show={this.state.showModal}
                    body={this.customInput()}
                    valid={this.state.fileTypeIndex !== "" && this.state.fieldTitle !== ""}
                    btnRight="Add"
                    onSubmit={() => this.handleAddCancelCustomField('add')}
                    onHide={() => this.handleAddCancelCustomField('cancel')}
                />
            </motion.div>
        )
    }
} 

export default withAlert()(CreateOrganisation); 